import React from 'react'

export default function LoginRegister() {
    return (
        <div>
            <div className='max-w-[1320px] mx-auto grid grid-cols-2 py-5 gap-5'>
                <div>
                    <h1 className='font-bold text-3xl'>Login</h1>
                    <div className='border-1 p-5 border-[#eee] mt-5'>
                        <form>
                            <label>Email *</label>
                            <input placeholder='Enter Email' className='w-[100%] border-1 py-3 border-[#eee] pl-2' type='text' />
                            <br /><br />
                            <label>Passowrd *</label>
                            <input placeholder='Enter Password' className='w-[100%] border-1 py-3 border-[#eee] pl-2' type='text' />

                            <br /><br />
                            <button className='bg-amber-500 p-2 w-[100px] rounded-2xl text-white'>Login</button>
                        </form>
                    </div>

                </div>
                <div>
                    <h1 className='font-bold text-3xl'>Register</h1>
                    <div className='border-1 p-5 border-[#eee] mt-5'>
                        <form>
                            <label>Email *</label>
                            <input placeholder='Enter Email' className='w-[100%] border-1 py-3 border-[#eee] pl-2' type='text' />
                            <br /><br />
                            <label>Passowrd *</label>
                            <input placeholder='Enter Password' className='w-[100%] border-1 py-3 border-[#eee] pl-2' type='text' />

                            <br /><br />
                            <button className='bg-amber-500 p-2 w-[100px] rounded-2xl text-white'>Register</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
